<template>
  <div class="setting">
    <el-form ref="form" :model="form" action="/setting" label-width="80px" class="param-form">
      <el-form-item label="种群大小">
        <el-input v-model.number="form.populationSize" type="number"></el-input>
      </el-form-item>
      <el-form-item label="迭代次数">
        <el-input v-model.number="form.generationCount" type="number"></el-input>
      </el-form-item>
      <el-form-item label="变异概率">
        <el-input v-model.number="form.rmp" type="number"></el-input>
      </el-form-item>
      <el-form-item label="选择方式">
        <el-radio-group v-model="form.selection_process">
          <el-radio label="elitist">精英主义</el-radio>
          <el-radio label="roulette wheel">轮盘赌</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="生成数据">
        <el-radio-group v-model="form.inputWay">
          <el-radio label="random">生成随机网络</el-radio>
          <el-radio label="upload">上传文件</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-upload
        class="upload-demo"
        action="https://up-z2.qiniup.com"
        v-if="form.inputWay === 'upload'"
        :data="token"
        :on-preview="handlePreview"
        :on-remove="handleRemove"
        :on-change="fileChange"
        :before-upload="beforeUpload"
        multiple
        accept=".txt"
        :file-list="fileList">
        <el-button size="small" type="text">点击上传</el-button>
        <div slot="tip" class="el-upload__tip">只能上传txt文件，以逗号或空格分离</div>
      </el-upload>
      <el-form :inline="true" :model="randomNetwork" v-if="form.inputWay === 'random'">
        <el-form-item label="网络数量">
          <el-input v-model.number="randomNetwork.numOfNetwork" type="number"></el-input>
        </el-form-item>
        <el-form-item label="节点个数">
          <el-input v-model.number="randomNetwork.numOfNode" type="number"></el-input>
        </el-form-item>
        <el-form-item label="边数">
          <el-input v-model.number="randomNetwork.numOfEdge" type="number"></el-input>
        </el-form-item>
      </el-form>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">保存</el-button>
        <el-button>重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'Setting',
  components: {
  },
  computed: {
    ...mapState(['form', 'Mats', 'randomNetwork'])
  },
  data: () => ({
    fileList: [],
    token: {
      token: '7TGcdFbAxxEz-RHC0t_ymQzWij5FhPL5RrgX2QYI:up6s1u0MZEUHDOo0AiLcrUPaYHI=:eyJzY29wZSI6InNob3J0ZXN0LXBhdGgyIiwiZGVhZGxpbmUiOjE2MjMwMzQ5MDF9'
    }
  }),
  created() {

  },
  mounted() {
    
  },
  methods: {
    onSubmit() {
      if (this.form.inputWay === 'random') {
        this.$store.commit('edit', {
          form: this.form, 
          Mats: [], 
          randomNetwork: this.randomNetwork
        })
        this.$router.push('/result')
        return
      }
      const Mats = []
      const listLen = this.fileList.length
      for (let t = 0; t < listLen; t++) {
        const file = this.fileList[t]
        if (file.raw) {
          let reader = new FileReader()
          reader.readAsText(file.raw, 'gb2312')
          reader.onload = (e) => {
            let str = e.target.result
            let lines = str.split(/\r\n|\r|\n/).filter(v => v)
            let dim = lines.length
            let Mat = new Array(dim + 1)
            for (let i = 0; i <= dim; i++) {
              Mat[i] = new Array(dim).fill(0)
            }
            for (let i = 0; i < lines.length; i++) {
              let nums = lines[i].split(/,|\s+/).map(a => +a)
              for (let j = 0; j < i; j++) {
                Mat[i + 1][j + 1] = Mat[j + 1][i + 1] = nums[j]
              }
            }
            Mats.push(Mat)

            if (t === listLen - 1) {
              this.$store.commit('edit', {
                form: this.form, 
                Mats: Mats, 
                randomNetwork: this.randomNetwork
              })
              this.$router.push('/result')
            }
          }
        }
      }
    },
    handleRemove(file, fileList) {
      this.fileList = fileList
    },
    handlePreview(file) {
    
    },
    fileChange(file, fileList) {
      this.fileList = fileList
    },
    beforeUpload(file) {
      let fileSuffix = file.name.substring(file.name.lastIndexOf('.') + 1)
      const res = fileSuffix === 'txt'
      if (!res) {
        this.$message({
          message: '上传文件只能是txt格式',
          type: 'warning'
        })
      }
      return res
    }
  }
}
</script>

<style scoped lang="scss">
.setting {
  .param-form {
    width: 40%;
    margin: 20px auto;
    .el-form-item {
      margin-top: 20px;
    }
  }
}
</style>
